* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}
.container {
  margin: 0 16px;
}
.btn {
  background: #555;
  border: "none";
}
.btn:hover {
  background-color: #323838;
}

@media screen and (max-width: 480px) {
  .container {
    margin: 0;
  }
}
